import React from 'react';

import { Link } from 'react-router-dom';

import '../../styles/footer.scss';

import {li2} from '../../assets/icons';

import {keti, komsa} from '../../assets/images';

import {checkbox} from '../../assets/icons';


const Footer = () => {
  return (
    <div className='footerWrap'>
        <footer>
        <div className='footer_text'>
            <ul className='footer_link'>
                <li><Link to="/"> </Link></li>
                <img src={li2} alt="" />
                <li><Link to="/"></Link></li>
                <img src={li2} alt="" />
                <li><Link to="/"></Link></li>
                <img src={li2} alt="" />
                <li><Link to="/"></Link></li>
                <img src={li2} alt="" />
                <li><Link to="/"></Link></li>
                <img src={li2} alt="" />
                <li><Link to="/">  </Link></li>
            </ul>

            <div className='footer_static'>
                <div>
                    <p> : (30100)   27 ()</p>
                    <p>Fax : 044-330-2357</p>
                    <p>Tel: 044-330-2380</p>
                </div>
                <p>Copyright  2020 KOMSA. All rights Reserved.</p>
            </div>
        </div>
        

        <div  className='footer_img'>
            <img src={keti} alt="" />
            <img src={komsa} alt="" />
            <div className='linksite'>
                <Link to="/"></Link>
                <img src={checkbox} alt="" />
            </div>
            
        </div>

    </footer>
    </div>
    
  );
};

export default Footer;