import { stringify } from 'querystring';
import React from 'react';

import { Link } from 'react-router-dom';


const NavBar = (props : any) => {


    let title: string = props.title;


    interface Menu {
        title: string;
        content: string;
    }

    let menu: Array<Menu> = [
        {title: 'KOMSA', content: 'AI     \n     '},
        {title: '', content: 'AI      \n   '},
        {title: '', content: '      \n '}
    ]   


    let content = menu.find(e => e.title === title)?.content || null;

    console.log(content);

    let subTitle = [];


    if(content === null){
        
    } else {
        subTitle.push(<div className='subTitleWrap'>
                        <div className='subTitle'>
                            <h1>{title}</h1>
                            <pre>{content}</pre>
                        </div>
                    </div>);

    }



  return (
    <div>
        {subTitle}
    </div>
    
            
  );
};

export default NavBar;