import { stringify } from 'querystring';
import React, {useEffect} from 'react';

import { Link, useParams, useSearchParams,  useNavigate  } from 'react-router-dom';


import {sideBar101, sideBar102, sideBar103, sideBar201, sideBar202, sideBar203, sideBar204, sideBar301, sideBar302,
    sideBar401, sideBar402, sideBar403, sideBar404, sideBar405, sideBar406, sideBar407,
    sideBar501, sideBar502, sideBar503, sideBar504, sideBar505, sideBar506}
    from '../../assets/icons/sideBar'


const SideBar = (props : any) => {
    const navigate = useNavigate();


    const menuTitle: Array<string> = [
        '',
        'KOMSA',
        '',
        '',
        '',
        ''
    ]

    
    
    interface menuRoute {
        readonly menuId: string;
        readonly menuTitle: string;
        readonly icon: string;
    }

    const komsa: Array<menuRoute> = [
        {menuId: '101', menuTitle: '', icon: sideBar101},
        {menuId: '102', menuTitle: ' ', icon: sideBar102},
        {menuId: '103', menuTitle: ' ', icon: sideBar103},
    ]

    const inform: Array<menuRoute> = [
        {menuId: '201', menuTitle: ' ', icon: sideBar201},
        {menuId: '202', menuTitle: ' ', icon: sideBar202},
        {menuId: '203', menuTitle: '/', icon: sideBar203},
        {menuId: '204', menuTitle: '', icon: sideBar204},
    ]

    const news: Array<menuRoute> = [
        {menuId: '301', menuTitle: ' ', icon: sideBar301},
        {menuId: '302', menuTitle: 'FAQ', icon: sideBar302},
    ]

    const design: Array<menuRoute> = [
        {menuId: '401', menuTitle: '', icon: sideBar401},
        {menuId: '402', menuTitle: '', icon: sideBar402},
        {menuId: '403', menuTitle: '', icon: sideBar403},
        {menuId: '404', menuTitle: '', icon: sideBar404},
        {menuId: '405', menuTitle: '', icon: sideBar405},
        {menuId: '406', menuTitle: '', icon: sideBar406},
        {menuId: '407', menuTitle: '3D  ', icon: sideBar407},
    ]

    const manage: Array<menuRoute> = [
        {menuId: '501', menuTitle: ' ', icon: sideBar501},
        {menuId: '502', menuTitle: ' ', icon: sideBar502},
        {menuId: '503', menuTitle: ' ', icon: sideBar503},
        {menuId: '504', menuTitle: 'DB ', icon: sideBar504},
        {menuId: '505', menuTitle: 'OPEN API', icon: sideBar505},
        {menuId: '506', menuTitle: '', icon: sideBar506},
    ]




    const [searchParams, setSearchParams] = useSearchParams();
    const params = useParams();

    

    useEffect(() => {
        let findIndex = menuTitle.findIndex(e => e === params.subTitle)
        
        if(searchParams.get('menuId') == null){
            setSearchParams({menuId: findIndex+'01'})

        }
    })

    let selectedMenu = searchParams.get('menuId');


    let lists = [];
    
    if(selectedMenu != null){
        let subTitleIndex: number = Number(selectedMenu.substring(0, 1));
        let index: number = Number(selectedMenu.substring(1, 3));

        let selectArr: Array<menuRoute> = [];

        console.log(selectedMenu)

        if(subTitleIndex === 1){
            selectArr = [...komsa];
        } else if(subTitleIndex === 2){
            selectArr = [...inform];
        } else if(subTitleIndex === 3){
            selectArr = [...news];
        } else if(subTitleIndex === 4){
            selectArr = [...design];
        } else if(subTitleIndex === 5){
            selectArr = [...manage];
        } else {
            selectArr = [];
        }


        console.log(selectArr);
        
        for(let i=0; i<selectArr.length; i++){
            let link:string = '/'+menuTitle[subTitleIndex]+'?menuId='+selectArr[i].menuId;
            let value:string = selectArr[i].menuTitle
            let icon:string = selectArr[i].icon

            let list = <li key={i} className={i===index-1 ? 'selected' : ''}>
                        <img src={icon} alt="" />
                        <Link to={link}>{value}</Link>
                       </li>;


            lists.push(list);

            console.log('/'+menuTitle[subTitleIndex]+'?menuId='+selectArr[i].menuId);



            
        }

        
    }
    

    
    
    
    

  return (
    <div className='sideBar'>
        <ul>
            {lists}
        </ul>

    </div>
            
  );
};

export default SideBar;